.App {
    text-align: center;
  }
  .App-header {
    background-color: #3c183d;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .background {
    width: 650px;
    height: 100vh;
    background-image: url(../../img/background.svg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .grama {
    height: 20vh;
    background-image: url(../../img/elements/grass.svg);
    width: 100%;
    background-position: bottom;
    background-size: cover;
    margin-top: auto;
  }
  
  @media only screen and (max-width: 1024px) {
    .background {
      width: 100vw;
    }
    /*.menu {
